import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import getters from './getters';
import mutations from './mutations';

Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        isLogin: false, // 
        isBLR: 0, // ,0-bind,1-login,2-register
        showLoginBox: false, // 
        mobileNbr: '',
        password: '',
        userCode: '',
        tokenCode: '',
        localCity: '', // 
        latitude: '30.878131',
        longitude: '120.137198',
        isChooseCity: false, // 
        zoneId: '', // id
        userCouponList: [],
        global: {
            hftcom: 'https://cloud.hfq.huift.com.cn',
            hftcomClient: 'https://cloud.hfq.huift.com.cn/Api/Client',
            hftcomShop: 'https://cloud.hfq.huift.com.cn/Api/Shop',
            hftcomWeixinCard: 'https://cloud.hfq.huift.com.cn/Api/WeiXinCard',
            hftcomEnCode: 'http%3a%2f%2f192.168.5.118%3a2000%2f%23%2f',
            hftcomOpenID: 'https://cloud.hfq.huift.com.cn/Admin/OpenId/getUserOpenId',
            hftcomGetUserInfo: 'https://cloud.hfq.huift.com.cn/Api/Shop',
            Daipay: 'https://cloud.hfq.huift.com.cn/Admin/Lcy/Daipay',
            Dipay: 'https://cloud.hfq.huift.com.cn/Admin/Lcy/DiPay',
            getAT: 'https://cloud.hfq.huift.com.cn/Admin/OpenId/useraaa'
        },
        weixin: {
            openId: '',
            code: '',
            htmldata: ''
        },
        indexCouponList: [], // 
        indexCouponDetailsInfo: {}, // 
        CouponListInfo: [], // 
        collectShopList: [], // 
        isbool: true, // 
        searchIsBool: true,
        searchShopList: [], // shop
        nearbyIsBool: true, // 
        nearbyShopList: [],  // shop
        searchConponIsBool: true, // 
        searchConponList: [], // shop
        searchTypeValue: 0, //   0  
        searchTypeName: '',  //     
        currentOrderInfo: [], // 
        isCancelOrder: false
    },
    getters,
    mutations,
    actions
});
export default store;



