import {mapState} from 'vuex';
export default{
    props: ['index'],
    data() {
        return {
            isGeting: false,  // 
            isCollect: false, // 
            isCollecting: false  // 
        };
    },
    computed: {
        ...mapState(['global', 'CouponListInfo', 'isBLR', 'isLogin', 'userCode', 'currentOrderInfo']),
      // 
        computedStreet() {
            if (this.index.street !== '' && this.index.street !== undefined) {
                return this.methodGetByteLen(this.index.street, 25);
            } else {
                return '';
            }
        },
        computedShopName() {
            if (this.index.shopName !== '' && this.index.shopName !== undefined) {
                return this.methodGetByteLen(this.index.shopName, 22);
            } else {
                return this.index.shopName;
            }
        },
        isCanGet: function() { // 
            if (this.index.countMyReceived === undefined || this.index.nbrPerPerson === '0') {
                return true;
            } else {
                if ((this.index.nbrPerPerson - this.index.countMyReceived) > 0) {
                    return true;
                } else {
                    return false;
                }
            }
        }
    },
    watch: {
        CouponListInfo: function() {
            this.filterIsCollect();
        }
    },
    methods: {
        toIndexCouponDetails: function(nowBatchCouponCode) {
            this.$router.push({path: `/indexCouponDetails/${nowBatchCouponCode}`});
        },
        grabCoupon: function(index) { // 0
            let that = this;
            this.axios({
                method: 'post',
                url: this.global.hftcomClient,
                data: '{"id": 67,"jsonrpc": "2.0","method": "grabCoupon","params": {"batchCouponCode": "' + index.batchCouponCode + '","userCode": "' + this.$store.state.userCode + '","sharedLvl": "0"}}',
                dataType: 'json'
            }).then((result) => {
                that.isGeting = false;
                if (result.data.result.code === '80222') {
                    that.$vux.toast.text('', 'bottom');
                    that.index.countMyReceived = that.index.nbrPerPerson;
                } else if (result.data.result.code === 50000) {
                    that.$vux.toast.text('', 'bottom');
                    if (result.data.result.isCount === 0) {
                        that.index.countMyReceived = that.index.nbrPerPerson;
                    } else {
                        that.isCanGet = true;
                    }
                } else {
                    that.$vux.toast.text(result.data.result, 'bottom');
                }
            });
        },
        addANDbank: function(index) { // 0
            let that = this;
            this.axios({
                method: 'post',
                url: that.global.hftcomClient,
                data: '{"id":19,"jsonrpc":"2.0","method":"addCouponOrder","params":{"userCode":"' + that.userCode + '","shopCode":"' + index.shopCode + '","batchCouponCode":"' + index.batchCouponCode + '","couponNbr":1,"platBonus":0,"shopBonus":0}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((order) => {
                if (order.data.result.code === 50000) {
                    this.axios({
                        method: 'post',
                        url: that.global.hftcomClient,
                        data: '{"id":19,"jsonrpc":"2.0","method":"bankcardPayConfirm","params":{"consumeCode":"' + order.data.result.consumeCode + '","bankAccountCode":"","valCode":""}}',
                        contentType: 'application/json',
                        dataType: 'json'
                    }).then((result) => {
                        that.isGeting = false;
                        if (result.data.result.code === 50000) {
                            that.$vux.toast.text('', 'bottom');
                        } else {
                            switch (result.data.result.code) {
                            case 80221:
                                that.$vux.toast.text('', 'bottom');
                                that.index.countMyReceived = that.index.nbrPerPerson;
                                break;
                            default:
                                that.$vux.toast.text(order.data.result.code, 'bottom');
                            }
                        }
                    });
                } else {
                    that.isGeting = false;
                    switch (order.data.result.code) {
                    case '80221':
                        that.$vux.toast.text('', 'bottom');
                        that.index.countMyReceived = that.index.nbrPerPerson;
                        break;
                    case '80238':
                        that.$vux.toast.text('', 'bottom');
                        that.index.countMyReceived = that.index.nbrPerPerson;
                        break;
                    case '80220':
                        that.$vux.toast.text('', 'bottom');
                        break;
                    default:
                        that.$vux.toast.text(order.data.result, 'bottom');
                    }
                }
            });
        },
        getCoupon: function(index) {
            let that = this;
            if (!this.isLogin) {
                this.$store.state.isBLR = 1;
                this.$store.state.showLoginBox = true;
                this.$vux.toast.text('', 'bottom');
            } else {
                that.isGeting = true;
                if ((index.payPrice === 0 || index.payPrice === '0') && index.couponType !== '7' && index.couponType !== '8') {
                  // 0
                    this.grabCoupon(index);
                    return;
                } else {
                    if (index.payPrice === 0) {
                    // 0
                        this.addANDbank(index);
                    } else {
                        // 
                        this.$router.push({path: `/submitorder/${index.batchCouponCode}`});
                    }
                }
            }
        },
        collectCoupon: function(code) {
            let that = this;
            if (!this.isLogin) {
                this.$store.state.isBLR = 1;
                this.$store.state.showLoginBox = true;
                this.$vux.toast.text('', 'bottom');
            } else {
                this.isCollecting = true;
                this.axios({
                    method: 'post',
                    url: this.global.hftcomShop,
                    data: '{"id":19,"jsonrpc":"2.0","method":"collectCoupon","params":{"batchCouponCode":"' + code + '","userCode":"' + this.$store.state.userCode + '"}}',
                    contentType: 'application/json',
                    dataType: 'json'
                }).then((result) => {
                    if (result.data.result === 1) {
                        that.isCollect = true;
                        that.$vux.toast.text('', 'bottom');
                    } else if (result.data.result === 0) {
                        that.isCollect = false;

                        that.$vux.toast.text('', 'bottom');
                    } else {
                        that.$vux.toast.text('', 'bottom');
                    }
                    that.isCollecting = false;
                });
            }
        },
        filterIsCollect: function() {
            let that = this;
            this.CouponListInfo.filter(function (element, index, self) {
                if (that.index.batchCouponCode === element.collectCouponCode) {
                    if (element.isCollect === '1') {
                        that.isCollect = true;
                    }
                }
            });
        },
        methodGetByteLen: function(str, len) { // 
            if (str.replace(/[^\x00-\xff]/g, '01').length <= len) {
                return str;
            }
            for (let i = Math.floor(len / 2); i < str.length; i++) {
                if (str.substr(0, i).replace(/[^\x00-\xff]/g, '01').length >= len) {
                    return str.substr(0, Math.floor(i / 2) * 2) + '...';
                }
            }
        }
    },
    created () {
        this.filterIsCollect();
    }
};



