import { mapState } from 'vuex';
export default{
    props: ['shopTypeList'],
    data() {
        return {
        };
    },
    methods: {
        goSearch(item) {
            // console.log($(el).attr('value')); // typeValue
            this.$store.state.searchTypeValue = item.typeValue; // vuex
            this.$store.state.searchTypeName = item.typeZh; // vuex
            this.$router.push({path: '/shops'});  // 
        }
    },
    computed: {
        ...mapState([
            'global'
        ])
    }
};



